@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: bold;
    margin: 0px 0px 0px -10px;
    padding-left: 7%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: absolute;
    z-index: 100;
    width: 95%;
    top: 0;
}

.topNav {
    float: right;
    padding: 0% 8.5% 0% 0%;
    font-size: 13px;
}
  
.navBar a:hover {
    color: white;
}
.footer {
    font-size: 25px;
    width: 98%;
    margin-left: 2%;
    font-style: italic;
    position: fixed;
    letter-spacing: .25em;
    background-color: white;
    bottom: 0;
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
  }

#footer-right {
  float: right;
  width: 20%;
}
  
.linkedin, .github {
  padding: 5px;
}

.email {
  padding: 0px 8% 0px 5px;
}
#about {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    margin: 115px 10%;
    height: 100vh;
}

h2 {
    letter-spacing: .075em;
}

.skills {
    display: flex;
    flex-direction: column;
    width: 35%;
    display: block;
}

.skills img {
    display: block;
    height: 500px;
}

.summary {
    width: 40%;
    text-align: left;
    line-height: 1.5em;
}
#contact-me { 
    background: linear-gradient(35deg, #ffffff 65%, #f79f99 45%);
    position: absolute;
    width: 100% !important;
    overflow-x: hidden !important;
    height: 100vh;
    text-align: left;
  }
  
  .contactText {
    width: 28%;
    float: left;
    padding: 12% 0% 0% 15%;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
  }

  .contactText img {
      max-height: 90px;
      object-fit: contain;
      margin-left: -55px;
  }
  
  .contactBox {
    width: 45%;
    float: right;
    display: inline-block;
    padding: 12% 1% 0% 0%;
    margin: 0px 0px 5% 2.5%;
  }
  
  #nameBox, #emailBox, #messageBox {
    border: 1px solid black;
    width: 68%;
    padding: 1.25%;
  }
  
  #nameBox, #emailBox {
    height: 20px;
  }
  
  #messageBox {
    height: 75px;
    display: flex;
    flex-wrap: wrap;
  }
  
  #sendMail {
    text-transform: uppercase;
    letter-spacing: .15em;
    background-color: white;
    color: black;
    border: 1.25px solid black;
    padding: 10px 20px;
    font-size: 1.1vw;
    margin-top: 15px;
  }
  
  #sendMail:hover {
    color: grey;
  }
#projects {
    background: #f7f7f7;
    min-height: 100vh;
    width: 100% !important;
    overflow-x: hidden !important;
}
  
.project-title {
    text-align: center;
    font-size: 45px;
    letter-spacing: .17em;
    padding: 55px 0px 0px 0px;
}

.first-row-project {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  margin: 5%;
}
  
#projects img {
    width: 500px;
    height: 275px; 
    object-fit: cover;
}
  
.first-project-left:hover, .first-project-right:hover {
    opacity: .80;
}
  
#welcome-page {
  min-height: 100vh;
  width: 100% !important;
  overflow-x: hidden !important;
}

.welcomeHeader {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 5vw;
    text-transform: lowercase;
    font-weight: bold;
    letter-spacing: .15em;
}
  
.typeWrite {
  font-family: 'Lexend Deca';
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(40, end);
          animation: 
    typing 3.5s steps(40, end);
}
  
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
  
.top {
    background-color: #f79f99;
    padding: 10% 0% .5% 0%;
    /* margin: -1vw -1vw 0 -1vw; */
    justify-content: center;
    display: flex;
    height: 50%;
}
  
.bottom {
    padding: 0% 25% 0% 10%;
    justify-content: center;
    align-items: center;
    display: flex;
}
  
.exploreButton {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1%;
    font-size: 17px;
}

.project-page {
    margin: 125px 22.5%;
}

.project-page h2 {
    letter-spacing: .2em;
}

.project-page p {
    margin: 0 5% 50px 5%;
    line-height: 1.5em;
    font-weight: 100;
}

.page-bottom {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 25px;
    text-align: left;
    line-height: 1.5em;
}

.page-bottom-left {
    border-right: 1px solid black;
    width: 55%;
    padding: 10px 0px;
}

.page-bottom-left a, .page-bottom-left a:visited {
    color: #f79f99;
}


